body {
  font-family: 'Poppins', sans-serif;
  padding-top: 80px;
  padding-left: 80px;
  min-height: 100vh;
  font-size: 1.5rem;
  background-color: #222222;
  color: #999999;
}

.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-bottom: 30px;
  width: 50%;
}

.form__field {
  font-family: inherit;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}

.form__field:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #f0c27b, #4b1248);
  border-image-slice: 1;
}

.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #f0c27b;
  font-weight: 700;
}

.form__field:required,
.form__field:invalid {
  box-shadow: none;
}

.country__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.country__item {
  list-style: none;
}

.country__info {
  list-style: none;
  margin: 0;
  padding: 0;
}
.country__info li:not(:last-child) {
  margin-bottom: 10px;
}

.country__info-wrapper {
  display: flex;
  align-items: center;
}

.country__name {
  color: #f0c27b;
}

.country__info {
  margin-right: 30px;
}

.country__language-wrapper,
.country__currency-wrapper {
  display: flex;
  align-items: baseline;
}
.country__language-title,
.country__currency-title {
  margin: 0;
}

.country__language-list,
.country__currency-list {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.country__languages-item,
.country__currency-item {
  list-style: none;
  padding-left: 15px;
}

.text-wrapper {
  color: #f0c27b;
}

.is-hidden {
  display: none;
}

.country__flag {
  filter: grayscale(70%);
}

.country__item--smaller {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country__flag--smaller {
  width: 50px;
  margin-right: 10px;
}

.swal-modal {
  position: absolute;
  max-width: 300px;
  bottom: 15px;
  left: 15px;
  padding: 10px;
  margin: 0;
  background: linear-gradient(#f0c27b, #f0c27b);
}

.swal-icon {
  display: none;
}

.swal-title:first-child {
  margin: 0;
  margin-bottom: 10px;
}

.swal-title {
  color: #4b1248;
  font-weight: 700;
  padding: 0;
  font-size: 18px;
}

.swal-text {
  color: rgba(0, 0, 0, 0.8);
  font-weight: 700;
  font-size: 14px;
  text-align: center;
}

.swal-text:last-child {
  margin: 0;
}

.swal-footer {
  margin: 0;
  padding: 0;
}

.swal-icon--warning__body {
  top: 8px;
}
.swal-icon--warning__dot {
  margin-left: -3px;
}

.error {
  background: linear-gradient(#ff416c, #ff4b2b);
  color: wheat;
}
.error .swal-title {
  color: wheat;
}
